import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ToastController, AlertController, LoadingController } from 'ionic-angular';
import { EventsProvider } from '../../providers/events/events';
import { FormControl } from '@angular/forms';

@IonicPage()
@Component({
  selector: 'page-events-view',
  templateUrl: 'events-view.html',
})
export class EventsViewPage {
  eventList: any;
  user: any;
  eventList2:any;
  searchControl: FormControl;
  searchTerm: string = '';
  searching: any = false;

  constructor(public alCtrl: AlertController,public loadingCtrl: LoadingController,public toastCtrl: ToastController,public navCtrl: NavController, public navParams: NavParams, private eventP:EventsProvider) {
    this.user = this.navParams.data;
    this.searchControl = new FormControl();
    console.log(this.user);
  }

  ionViewWillEnter(){
     let loading = this.loadingCtrl.create({content: 'Please wait...'});
    loading.present();
    const toast = this.toastCtrl.create({
      message: 'Events were loaded successfully',
      duration: 3000,
      position: 'middle',
       cssClass: "toastClass",
    });
    this.eventP.getEventList().valueChanges().subscribe( list =>{
      this.eventList = list;
      this.eventList2 = list;
      console.log(this.eventList);
      loading.dismiss();
      toast.present();
      this.searchControl.valueChanges.subscribe(search => {
          this.searching = false;
          this.setFilteredItems();
      });
    })
    console.log(this.eventList)
  }
  deleteEvent(id){
  console.log(id);
  this.eventP.deleteAEvent(id).then( res => {
    //this.showAlert("Success","Event delete success")
    const toastS = this.toastCtrl.create({
      message: 'Event deleted successfully',
      duration: 3000,
      position: 'middle',
      cssClass: "toastClass",
    });
    toastS.present();
    console.log(res);
  }).catch( err =>{
    //this.showAlert("Failed",err.message)
    const toastF = this.toastCtrl.create({
      message: err.message,
      duration: 3000,
      position: 'middle',
      cssClass: "toastClass",
    });
    console.log(err);
  })
  }
  showAlert(header, msg) {
   const alert = this.alCtrl.create({
    title: header,
    subTitle: msg,
    buttons: ['OK']
   });
   alert.present();
  }
  filterItems(searchTerm){
         return this.eventList.filter((item) => {
             return item.name.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
         });
    }
  setFilteredItems() {
    this.eventList = this.eventList2;
      this.eventList = this.filterItems(this.searchTerm);
  }
  onSearchInput(){
      this.searching = true;
  }
}



