import { Component } from '@angular/core';
import { IonicPage, NavController, ModalController, NavParams, LoadingController } from 'ionic-angular';
import { FormControl } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { AlertController } from 'ionic-angular';


@IonicPage()
@Component({
  selector: 'page-library',
  templateUrl: 'library.html',
})
export class LibraryPage {

  books: any;
  searchTerm: any;
  searchControl: FormControl;
  maxResults: any;
  orderBy: any;
  customize: boolean;
  constructor(public modalCtrl: ModalController, public alertCtrl: AlertController,public loadingCtrl: LoadingController,public httpClient: HttpClient,public navCtrl: NavController, public navParams: NavParams) {
    this.searchControl = new FormControl();
    this.books = [];
    //this.searchTerm = 'C Programming';
    this.customize = false;
    this.orderBy = 'relevance';
    this.maxResults = 15;
  }

  searchBooks(){
    const loading = this.loadingCtrl.create({content: 'Please wait...'});
    loading.present();
    this.httpClient.get('https://www.googleapis.com/books/v1/volumes?q='+this.searchTerm+"&maxResults="+this.maxResults+"&orderBy="+this.orderBy).subscribe( data => {
      this.books = data['items'];
      console.log(data);
      loading.dismiss();
    })
  }

  modifyQuery(){
    this.customize = !this.customize;
  }

  viewBook(book){
    const modal = this.modalCtrl.create('BookInfoPage', {"data": book})
    modal.present();
  }

}



