import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HttpClient } from '@angular/common/http';
import { FormControl } from '@angular/forms';
import { FacultyDetailPage } from '../faculty-detail/faculty-detail';

@IonicPage()
@Component({
  selector: 'page-faculty-list',
  templateUrl: 'faculty-list.html',
})
export class FacultyListPage {
  searchControl: FormControl;

  searchTerm: string = '';
  searching: any = false;
  faculties: any;
  temp: any;
  constructor(public navCtrl: NavController, public navParams: NavParams,  public httpClient: HttpClient) {
    this.initializeData();
    this.searchControl = new FormControl();
  }

  initializeData(){
    this.httpClient.get('assets/data_rym_faculty.json').subscribe( data => {
      console.log(data);
      this.faculties = data;
      this.temp = data;
    })
  }

  ionViewDidLoad() {
      this.initializeData();
        this.searchControl.valueChanges.subscribe(search => {
            this.searching = false;
            this.setFilteredItems();
        });


    }

    onSearchInput(){
        this.searching = true;
    }
    filterItems(searchTerm){
           return this.temp.filter((item) => {
               return item.name.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
           });
      }
    setFilteredItems() {
        this.faculties = this.filterItems(this.searchTerm);
    }
    showDetails(faculty){
      this.navCtrl.push(FacultyDetailPage,{"data": faculty});
    }
}



