import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { EventsViewPage } from '../events-view/events-view';
import { EventsAddPage } from '../events-add/events-add';
/**
 * Generated class for the EventsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-events',
  templateUrl: 'events.html',
})
export class EventsPage {

  tab1Root = EventsViewPage;
  tab2Root = EventsAddPage;
  user: any;
  constructor(public navP: NavParams){
    this.user  = this.navP.get('user');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EventsPage');
    this.user  = this.navP.get('user');
  }

}



