import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { NotesViewPage } from '../notes-view/notes-view';
import { NotesUploadPage } from '../notes-upload/notes-upload';

@IonicPage()
@Component({
  selector: 'page-notes',
  templateUrl: 'notes.html',
})
export class NotesPage {
  tab1Root = NotesUploadPage;
  tab2Root = NotesViewPage;
  user: any;
  constructor(public navCtrl: NavController, public navP: NavParams) {
    this.user  = this.navP.get('user');
  }

    ionViewDidLoad() {
      console.log('ionViewDidLoad EventsPage');
      this.user  = this.navP.get('user');
    }

}



