import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import { NotesPage } from '../pages/notes/notes';
import { NotesViewPage } from '../pages/notes-view/notes-view';
import { NotesUploadPage } from '../pages/notes-upload/notes-upload';
import { DashboardPage } from '../pages/dashboard/dashboard';
import { StudentsPage } from '../pages/students/students';
import { FacultiesPage } from '../pages/faculties/faculties';
import { LibraryPage } from '../pages/library/library';
import { MenuPageModule } from '../pages/menu/menu.module';

import {HttpClientModule} from '@angular/common/http';//import
import { FacultyListPage } from '../pages/faculty-list/faculty-list';
import { FacultyDetailPage } from '../pages/faculty-detail/faculty-detail';

import { AngularFirestore } from 'angularfire2/firestore';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireModule } from 'angularfire2';
import { FIREBASE_CONFIG } from '../providers/events/fire.conf';
import { AngularFireAuthModule  } from 'angularfire2/auth';
import { AngularFireStorageModule } from 'angularfire2/storage';
import { AngularFireDatabase} from 'angularfire2/database';
import { FileSizePipe } from '../pipes/file-size/file-size';


import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { EventsViewPage } from '../pages/events-view/events-view';
import { EventsAddPage } from '../pages/events-add/events-add';
import { EventsPage } from '../pages/events/events';
import { EventsProvider } from '../providers/events/events';
import { NotesProvider } from '../providers/notes/notes';

import { LoginPage } from '../pages/login/login';
import { RegisterPage } from '../pages/register/register';
import { AuthProvider } from '../providers/auth/auth';
import { StudentsProvider } from '../providers/students/students';
import {  MomentPipe} from '../pipes/moment/moment';

@NgModule({
  declarations: [
    MyApp,FileSizePipe,MomentPipe,
    AboutPage,/*MenuPage,*/FacultyListPage,FacultyDetailPage,
    ContactPage,StudentsPage, FacultiesPage, LibraryPage, DashboardPage,
    HomePage, NotesPage, NotesViewPage, NotesUploadPage,RegisterPage,
    TabsPage,EventsPage,EventsViewPage, EventsAddPage, LoginPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HttpClientModule,
    AngularFireModule.initializeApp(FIREBASE_CONFIG),
    MenuPageModule,
    AngularFirestoreModule,
    AngularFireStorageModule,
    AngularFireAuthModule,

  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,/*MenuPage,*/FacultyListPage,FacultyDetailPage,
    ContactPage,StudentsPage, FacultiesPage, LibraryPage, DashboardPage,
    HomePage,NotesPage, NotesViewPage, NotesUploadPage,RegisterPage,
    TabsPage,EventsPage,EventsViewPage, EventsAddPage, LoginPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    EventsProvider,  AngularFirestore,AngularFireDatabase,
    NotesProvider,
    AuthProvider,
    StudentsProvider
  ]
})
export class AppModule {}



