import { Injectable } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFirestore, AngularFirestoreDocument } from 'angularfire2/firestore';
import 'rxjs/add/operator/switchMap';
interface Person {
  uid: string;
  appid: string;
  role: string;
  created_on: any;
  last_used: any;
}

@Injectable()
export class AuthProvider {

  public isLoggedIn = false;
  constructor(public afAuth: AngularFireAuth,private afs: AngularFirestore){}
  async loginUser(newEmail: string, newPassword: string): Promise<any> {
    const ref = await this.afAuth.auth.signInWithEmailAndPassword(newEmail, newPassword);
    return ref;
  }
  authenticated() : boolean {
    return this.isLoggedIn;
  }
  resetPassword(email: string): Promise<void> {
    return this.afAuth.auth.sendPasswordResetEmail(email);
  }
  logoutUser(): Promise<void> {
    return this.afAuth.auth.signOut();
  }
  async signupUser(newEmail: string, newPassword: string, role: string): Promise<any> {
    const person =  await this.afAuth.auth.createUserWithEmailAndPassword(newEmail, newPassword);
    //return person
    return this.setPersonRecord(person,role);
  }

  setPersonRecord(person,role: string){
    console.log("sert record person: ",person);
    const personRef: AngularFirestoreDocument<Person> = this.afs.doc(`people/${person.user.uid}`);
    console.log("New user id: ",person.user.id);
    const data: Person = {
      uid: person.user.uid,
      appid: person.user.email || person.user.usn,
      role: role,
      created_on: new Date().getTime().toString(),
      last_used: new Date().getTime().toString()
    }
    //return null;
    return personRef.set(data);
  }
  getPersonRecord(id){
    return this.afs.collection('people').doc(id).valueChanges();
  }
  set_last_login(id){
    console.log(id);
    return this.afs.collection('people').doc(id).update({last_used: new Date().getTime().toString()});
  }
}



