import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormControl } from '@angular/forms';
import { ToastController, AlertController, LoadingController } from 'ionic-angular';
import { StudentsProvider } from '../../providers/students/students';

@IonicPage()
@Component({
  selector: 'page-students',
  templateUrl: 'students.html',
})
export class StudentsPage {

  students: any;
  students2:any;
  searchControl: FormControl;
  searchTerm: string = '';
  searching: any = false;
  role: string;
  constructor(public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,public navCtrl: NavController, public navParams: NavParams, private fileh: StudentsProvider) {
    this.role = 'all';
    this.getStudentsList();
    this.searchControl = new FormControl();
  }

  ionViewDidLoad() {
    this.searchControl.valueChanges.subscribe(search => {
        this.searching = false;
        this.setFilteredItems();
    });
    console.log('ionViewDidLoad NotesViewPage');
  }
  getStudentsList(){
    const loading = this.loadingCtrl.create({content: 'Please wait...'});
    loading.present();
    this.fileh.getStudentsList().valueChanges().subscribe( data =>{
      this.students = data;
      this.students2 = data;
      console.log(data);
      loading.dismiss();
    });
  }
  onSearchInput(){
      this.searching = true;
  }
  filterItems(searchTerm){
         return this.students2.filter((item) => {
             return item.appid.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
         });
    }
  setFilteredItems() {
    console.log(this.searchTerm, this.role);
      this.students = this.ffilterItems(this.searchTerm, this.role);
  }


  ffilterItems(searchTerm, filterType){
    this.students = this.students2;
    let temp = (this.students.filter((item) => {
        return (item.role.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1)
    }));
    if(filterType==="all"){
        console.log("In OE filter");
          return temp.filter((a) => {
            return a;
          });
    }
    else if(filterType==="student"){
        console.log("In student filter");
          return temp.filter((a) => {
            return a.role === "student"
          });
    }
    else if(filterType==="faculty"){
        console.log("In faculty filter");
          return temp.filter((a) => {
            return a.role === "faculty"
          });
    }
  }

  showSortRadio() {
     let alert = this.alertCtrl.create({cssClass: 'alertCustomCss'});
     alert.setTitle('Sort Type');
     let All, Student, Faculty;
     All = {
       type: 'radio',
       label: 'All',
       value: 'all',
     };
     Student = {
       type: 'radio',
       label: 'Student',
       value: 'student',
     };
     Faculty = {
       type: 'radio',
       label: 'Faculty',
       value: 'faculty',
     };
     alert.addInput(All);
     alert.addInput(Student);
     alert.addInput(Faculty);
     alert.addButton('Cancel');
     alert.addButton({
       text: 'OK',
       handler: data => {
         this.role = data;
         console.log(this.role);
         this.setFilteredItems();
       }
     });
     alert.present();
 }


}



