import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormControl } from '@angular/forms';
import { HttpClient } from '@angular/common/http';

@IonicPage()
@Component({
  selector: 'page-faculties',
  templateUrl: 'faculties.html',
})
export class FacultiesPage {

  searchControl: FormControl;
  searchTerm: string = '';
  searching: any = false;
  faculties: any;
  temp: any;
  constructor(public navCtrl: NavController, public navParams: NavParams, public httpClient: HttpClient) {
    this.initializeData();
    this.searchControl = new FormControl();
  }

  initializeData(){
    this.httpClient.get('assets/json/faculties.json').subscribe( data => {
      console.log(data);
      this.faculties = data;
      this.temp = data;
    })
  }

  ionViewDidLoad() {
      this.initializeData();
        this.searchControl.valueChanges.subscribe(search => {
            this.searching = false;
            this.setFilteredItems();
        });


    }

    onSearchInput(){
        this.searching = true;
    }
    filterItems(searchTerm){
           return this.temp.filter((item) => {
               return item.first_name.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
           });
      }
    setFilteredItems() {
        this.faculties = this.filterItems(this.searchTerm);
    }

}



