import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { NotesProvider } from '../../providers/notes/notes';
import { FormControl } from '@angular/forms';
import { ToastController, AlertController, LoadingController } from 'ionic-angular';


@IonicPage()
@Component({
  selector: 'page-notes-view',
  templateUrl: 'notes-view.html',
})
export class NotesViewPage {

  user: any;
  files: any;
  files2:any;
  searchControl: FormControl;
  searchTerm: string = '';
  searching: any = false;
  semtr: string;
  constructor(public alertCtrl: AlertController,public loadingCtrl: LoadingController,public navCtrl: NavController, public navParams: NavParams, private fileh: NotesProvider) {
    this.semtr = 'all';
    this.user = this.navParams.data;
    console.log("notes view user", this.user);
    this.getFilesList();
    this.searchControl = new FormControl();
  }

  ionViewDidLoad() {
    this.searchControl.valueChanges.subscribe(search => {
        this.searching = false;
        this.setFilteredItems();
    });
    console.log('ionViewDidLoad NotesViewPage');
  }
  getFilesList(){
    const loading = this.loadingCtrl.create({content: 'Please wait...'});
    loading.present();
    this.fileh.getFilesList().valueChanges().subscribe( data =>{
      this.files = data;
      this.files2 = data;
      console.log(data);
      loading.dismiss();
    });
  }
  onSearchInput(){
      this.searching = true;
  }
  filterItems(searchTerm){
         return this.files2.filter((item) => {
             return item.appid.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1;
         });
    }
  setFilteredItems() {
    console.log(this.searchTerm, this.semtr);
      this.files = this.ffilterItems(this.searchTerm, this.semtr);
  }


  ffilterItems(searchTerm, filterType){
    this.files = this.files2;
    let temp = (this.files.filter((item) => {
        return (item.fname.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1)
    }));
    if(filterType==="all"){
        console.log("In OE filter");
          return temp.filter((a) => {
            return a;
          });
    }
    else if(filterType==="1"){
        console.log("In student filter");
          return temp.filter((a) => {
            return a.sem === "1"
          });
    }
    else if(filterType==="2"){
        console.log("In faculty filter");
          return temp.filter((a) => {
            return a.sem === "2"
          });
    }
    else if(filterType==="3"){
        console.log("In faculty filter");
          return temp.filter((a) => {
            return a.sem === "3"
          });
    }
    else if(filterType==="4"){
        console.log("In faculty filter");
          return temp.filter((a) => {
            return a.sem === "4"
          });
    }
    else if(filterType==="5"){
        console.log("In faculty filter");
          return temp.filter((a) => {
            return a.sem === "5"
          });
    }
    else if(filterType==="6"){
        console.log("In faculty filter");
          return temp.filter((a) => {
            return a.sem === "6"
          });
    }
    else if(filterType==="7"){
        console.log("In faculty filter");
          return temp.filter((a) => {
            return a.sem === "7"
          });
    }
    else if(filterType==="8"){
        console.log("In faculty filter");
          return temp.filter((a) => {
            return a.sem === "8"
          });
    }
  }

  showSortRadio() {
     let alert = this.alertCtrl.create({cssClass: 'alertCustomCss'});
     alert.setTitle('Sort Type');
     let All, st1, st2,st3,st4,st5,st6,st7,st8;
     All = {
       type: 'radio',
       label: 'All',
       value: 'all',
     };
     st1 = {
       type: 'radio',
       label: '1st Sem',
       value: '1',
     };
     st2 = {
       type: 'radio',
       label: '2nd Sem',
       value: '2',
     };
     st3 = {
       type: 'radio',
       label: '3rd Sem',
       value: '3',
     };
     st4 = {
       type: 'radio',
       label: '4th Sem',
       value: '4',
     };
     st5 = {
       type: 'radio',
       label: '5th Sem',
       value: '5',
     };
     st6 = {
       type: 'radio',
       label: '6th Sem',
       value: '6',
     };
     st7= {
       type: 'radio',
       label: '7th Sem',
       value: '7',
     };
     st8 = {
       type: 'radio',
       label: '8th Sem',
       value: '8',
     };
     alert.addInput(All);
     alert.addInput(st1); alert.addInput(st2); alert.addInput(st3); alert.addInput(st4);
     alert.addInput(st5); alert.addInput(st6); alert.addInput(st7); alert.addInput(st8);
     alert.addButton('Cancel');
     alert.addButton({
       text: 'OK',
       handler: data => {
         this.semtr = data;
         console.log(this.semtr);
         this.setFilteredItems();
       }
     });
     alert.present();
 }

  deleteFile(path,file){
      const loading = this.loadingCtrl.create({content: 'Please wait...'});
      loading.present();
      this.fileh.deleteFileById(path,file);
      setTimeout(function() {
        loading.dismiss();
      }, 3000);
  }
}



