import { Component, ViewChild } from '@angular/core';
import { App,IonicPage, NavController,Nav , NavParams } from 'ionic-angular';
import { LoginPage } from '../login/login';
import { EventsPage } from '../events/events';
import { NotesPage } from '../notes/notes';
import { ContactPage } from '../contact/contact';
import { DashboardPage } from '../dashboard/dashboard';
import { StudentsPage } from '../students/students';
import { FacultiesPage } from '../faculties/faculties';
import { LibraryPage } from '../library/library';
import { FacultyListPage } from '../faculty-list/faculty-list';
import { TabsPage } from '../tabs/tabs';
import { AuthProvider} from '../../providers/auth/auth';
@IonicPage()
@Component({
  selector: 'page-menu',
  templateUrl: 'menu.html',
})
export class MenuPage {

  @ViewChild(Nav) nav: Nav;
  pages: any;
  user: any;
  rootPage = DashboardPage;

  constructor(public authData:AuthProvider,public app: App,public navCtrl: NavController, public navParams: NavParams) {
    console.log(this.navParams.get('role'));
    this.pages = [
      { title: 'Dashboard', component: DashboardPage, icon: 'ios-speedometer-outline'},
      { title: 'Library', component: LibraryPage, icon: 'ios-glasses-outline' },
      { title: 'Notes', component: NotesPage, icon: 'ios-albums-outline'},
      { title: 'Events', component: EventsPage, icon: 'ios-calendar-outline'},
      { title: 'Faculties', component: FacultyListPage, icon: 'ios-school-outline'},
      { title: 'Users', component: StudentsPage, icon: 'ios-people-outline'},
      { title: 'Developers', component: ContactPage, icon: 'ios-person-add-outline'},
      { title: 'Log out', component: '', icon: 'ios-power-outline'}
    ];

  }

  ionViewDidLoad() {
    if(this.authData.authenticated()){
      console.log('ionViewDidLoad WelcomePage');
      console.log(this.navParams.get('user'));
      this.user = this.navParams.get('user');
      console.log(this.user);
      this.nav.setRoot(DashboardPage,{user:this.navParams.get('user')});
    }
    else
        this.nav.setRoot(TabsPage);
  }

  openPage(page) {
    if(page.title == 'Log out'){
      this.authData.isLoggedIn = false;
      this.app.getRootNavs()[0].setRoot(TabsPage);
    }
    else
      this.nav.setRoot(page.component, {user:this.navParams.get('user')});
  }

}



