import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { AngularFireStorage, AngularFireUploadTask } from 'angularfire2/storage';
import { Observable } from 'rxjs/Observable';
import { AngularFirestore } from 'angularfire2/firestore';
import { tap, finalize } from 'rxjs/operators';
import { NotesProvider } from '../../providers/notes/notes';
import { FileSizePipe } from '../../pipes/file-size/file-size';
import {  FormBuilder, FormGroup, Validators } from '@angular/forms';

@IonicPage()
@Component({
  selector: 'page-notes-upload',
  templateUrl: 'notes-upload.html',
})
export class NotesUploadPage {

  task: AngularFireUploadTask;
  percentage: Observable<number>;
  snapshot: Observable<any>;
  downloadURL: Observable<string>;
  isHovering: boolean;
  person: string;
  notes_form: FormGroup;
  user: any;
  constructor(private db: AngularFirestore,public navParams: NavParams,public navCtrl: NavController,  private storage: AngularFireStorage, private fileh: NotesProvider, public formBuilder: FormBuilder) {
    this.person = "kiran";
    this.user = this.navParams.data;
    this.notes_form = this.formBuilder.group({
     'uploader': ['', [
      Validators.required,
      Validators.minLength(3),
     ]],
     'semester': ['',
      Validators.required
     ],
     'subject': ['',[
       Validators.minLength(2),
      Validators.required]
    ],
    'priority': ['',Validators.required]
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NotesUploadPage');
  }
  startUpload(event: FileList) {
    const file = event.item(0)
    console.log(file.type.split('/')[0]);
    /*if (file.type.split('/')[0] !== 'image') {
      console.error('unsupported file type :( ')
      return;
    }*/
    const path = `notes/${new Date().getTime()}_${file.name}`;
    const customMetadata = { uploader: this.person, upload_time: new Date().getTime().toString(), priority: 'low' };
    const fileRef = this.storage.ref(path);
    this.task = this.storage.upload(path, file, { customMetadata })
    this.percentage = this.task.percentageChanges();
    const id = this.db.createId();
    this.snapshot = this.task.snapshotChanges().pipe(
      finalize( () => {
        let fileR = this.storage.ref(path);
        fileR.getDownloadURL().subscribe( url => {
          console.log(url);
          let d = new Date();
          this.fileh.uploadFile(id,path,this.g_uploader.value,d.toISOString() , this.g_priority.value, url, file.name, this.g_semester.value, this.g_subject.value);
        })
      })
    )
    console.log(this.snapshot);
  }
  isActive(snapshot) {
  return snapshot.state === 'running' && snapshot.bytesTransferred < snapshot.totalBytes
  }

  get g_uploader(){
    return this.notes_form.get('uploader');
  }
  get g_subject(){
    return this.notes_form.get('subject');
  }
  get g_semester(){
    return this.notes_form.get('semester');
  }
  get g_priority(){
    return this.notes_form.get('priority');
  }

}



